define('modules/components/helpers/welcome-view',['require','modules/core/base-view','tmpl!modules/components/helpers/welcome-view'],function(require) {

  var BaseView = require('modules/core/base-view');
  var template = require('tmpl!modules/components/helpers/welcome-view');

  return BaseView.extend({
    template: template,

    events : {
      'click .close' : 'close'
    },

    initialize: function(options) {
      options = options || {};
      if (options.user) {
        this.user = options.user;
      }
    },

    serialize: function() {
      return {
        user: this.user
      };
    },

    close: function(ev) {
      this.$el.fadeOut();
      this.trigger('welcome:close');
      return false;
    }
  });

});

